canvas {
  z-index: 1;
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  font-family: "Merriweather", serif;
}

.errorPage {
  z-index: 3;
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100vh;
  background: radial-gradient(
    ellipse at center,
    hsla(0, 0%, 0%, 0) 0%,
    hsla(0, 0%, 0%, 0) 19%,
    hsla(0, 0%, 0%, 0.9) 100%
  );
  filter: progid:DXImageTransform.Microsoft.gradient(startColorstr = '#00000000', endColorstr = '#e6000000', GradientType = 1);
}

.errorPage div {
  position: absolute;
  left: 0;
  top: -20%;
  width: 100%;
  height: 20%;
  background-color: hsla(0, 0%, 0%, 0.09);
  box-shadow: 0 0 10px hsla(0, 0%, 0%, 0.2);
  animation: waves 12s linear infinite;
}

.errorPage div:nth-child(1) {
  animation-delay: 0;
}

.errorPage div:nth-child(2) {
  animation-delay: 4s;
}

.errorPage div:nth-child(3) {
  animation-delay: 8s;
}

@keyframes waves {
  0% {
    top: -20%;
  }
  100% {
    top: 100%;
  }
}

.errorNum {
  z-index: 3;
  position: absolute;
  font: bold 20vw "Merriweather", serif;
  left: 50%;
  top: 50%;
  margin-top: -10vh;
  width: 100%;
  margin-left: -50%;
  height: 20vw;
  text-align: center;
  color: transparent;
  text-shadow: 0 0 30px hsla(0, 0%, 0%, 0.5);
  animation: flicks 0.8s linear infinite;
}

.errorTitle {
  z-index: 2;
  position: absolute;
  font: bold 5.5vw "Merriweather", serif;
  left: 51%;
  top: 40%;
  width: 90%;
  margin-left: -45%;
  height: 50vw;
  text-align: center;
  color: transparent;
  text-shadow: 0 0 12px hsla(0, 0%, 0%, 0.5);
  animation: flicks 1.5s linear infinite;
}
