@keyframes flicks {
  0% {
    text-shadow: 0 0 30px hsla(0, 0%, 0%, 0.5);
  }
  33% {
    color: hsla(0, 0%, 0%, 0.4);
    text-shadow: 0 0 10px hsla(0, 0%, 0%, 0.4);
  }
  66% {
    color: transparent;
    text-shadow: 0 0 20px hsla(0, 0%, 0%, 0.2);
  }
  100% {
    color: hsla(0, 0%, 0%, 0.5);
    text-shadow: 0 0 40px hsla(0, 0%, 0%, 0.8);
  }
}

@keyframes spanflicks {
  0% {
    text-shadow: 0 0 30px hsla(0, 0%, 0%, 0.5);
  }
  33% {
    color: hsla(0, 0%, 0%, 0.4);
    text-shadow: 0 0 10px hsla(2, 95%, 15%, 0.5);
  }
  66% {
    color: transparent;
    text-shadow: 0 0 20px hsla(2, 95%, 15%, 0.2);
  }
  100% {
    color: hsla(0, 0%, 0%, 0.5);
    text-shadow: 0 0 40px hsla(2, 95%, 15%, 0.1);
  }
}
